.navbar{
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #171c22;
    display: flex;
    justify-content: space-between;
    border-top: 0px;
    border-left: 0px;
    border-bottom: 15px; 
    border-image: linear-gradient(#023030, #13aaaa) 50;
    border-style: solid;
}
.myname{
    
    color: #fdfdfd;
    border: 1px solid #1bf8f8;
    border-radius: 4px;
    margin: 10px 15px 10px 20px;
    padding: 10px 12px;
}
.index{
    display: flex;
}
.indx{
    display: flex;
    align-items: center;
    padding: 1rem;
    
}
a{
    text-decoration: none;
    color: #fbffff;
}
a:hover{
    color: #1bf8f8;
}